import { Component, OnInit, Injector, ViewChild, ViewEncapsulation } from '@angular/core';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { MatDrawer } from '@angular/material/sidenav';

import _moment from "moment-timezone";
import * as _ from 'underscore';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-personal-staff',
  templateUrl: './personal-staff.component.html',
  styleUrls: ['./personal-staff.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class PersonalStaffComponent implements OnInit {
  isCategoryDataLoaded = false;
  categoryOffcialListData: Array<any> = [];
  staffCategory: any;
  staffType: any;
  isEdit: boolean = false;
  staffCategoryId: any;
  modalService: any;
  isShowForm=false;
  categorySearch='';
  @ViewChild('association', {static: true})
  matDrawer: MatDrawer;

  timeZone:any;
  apiSubscribe:Subscription;

  constructor(private lookupService: LookupService,
    private sessionService: SessionService,
    private injector: Injector,
    private sharedService: SharedService) {
    this.modalService = this.injector.get(ModalService);
  }

  ngOnInit(): void {

    this.sharedService.timezonecast.subscribe( timeZone => this.timeZone = timeZone )

    this.getAssociateStaff();
    this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe(lookupValueId => {
      if (lookupValueId != null) {
        var params = {
          lookupValueId: lookupValueId,
          updateUserId: parseInt(this.sessionService.userId)
        }
        this.lookupService.deleteLookupvalue(params).subscribe((res: any) => {
          this.sharedService.openSnackBar('Personal Category Deleted Successfully','success');
          this.sharedService.setUnitListDeleteIndex(null);
          this.getAssociateStaff();
        }, error => {
        });
      }
    })
  }

  getAssociateStaff() {
    this.isCategoryDataLoaded = false;
    let params = {
      LookupTypeId: 27,
      ApartmentId: this.sessionService.apartmentId
    }

    this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
      this.categoryOffcialListData = res.filter(item => {
        return item.isActive;
      });
      this.isCategoryDataLoaded = true;
    },
      error => {

      }
    );
  }

  editStaffCategory(category) {
    this.isEdit = true;
    this.staffCategory = category.lookupValueName;
    this.staffCategoryId = category.lookupValueId;
    this.matDrawer.toggle();
  }
  addCategory(){
    this.isEdit = false;
    this.staffType = '';
    this.staffCategory = '';
    this.matDrawer.toggle();
  }
  deleteAssociate(lookupValueId) {
    this.modalService.showConfirmModal(lookupValueId);
  }
  submitAssociate() {
    if (!this.isEdit) {
      let details = {
        "apartmentId":this.sessionService.apartmentId,
        "lookupTypeId": 27,
        "lookupValueName": this.staffCategory,
        "description": this.staffCategory,
        "isActive": true,
        "insertedBy": parseInt(this.sessionService.userId),
        "insertedOn": _moment().toISOString(),
        "updatedBy": null,
        "updatedOn": null
      }
      let params = {
        lookupvalue: details
      }
      this.lookupService.addLookupValue(params).subscribe((res: any) => {
        this.sharedService.openSnackBar('Personal Category Added', 'success');
        this.isShowForm = false;
        this.matDrawer.toggle();
        this.getAssociateStaff();
      });
    } else {
      let details = {
        "apartmentId":this.sessionService.apartmentId,
        "lookupValueId": this.staffCategoryId,
        "lookupTypeId": 27,
        "lookupValueName": this.staffCategory,
        "description": this.staffCategory,
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": _moment().toISOString(),
        "updatedBy": parseInt(this.sessionService.userId),
        "updatedOn": _moment().toISOString()
      }

      let params = {
        lookupvalue: details
      }

      this.lookupService.updateLookupValue(params).subscribe((res: any) => {
        this.sharedService.openSnackBar('Personal Category Updated', 'success');
        this.isShowForm = false;
        this.matDrawer.toggle();
        this.getAssociateStaff();
      })
    }
  }

  ngOnDestroy(): void {
    this.apiSubscribe.unsubscribe();
  }

}
