import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from 'src/app/core/auth/guards/auth.guard';

import { StaffSetupComponent } from './components/staff-setup/staff-setup.component';
import { StaffMaintainStaffComponent } from './components/staff-maintain-staff/staff-maintain-staff.component';
import { StaffInactiveStaffComponent } from './components/staff-inactive-staff/staff-inactive-staff.component';
import { StaffAttendanceComponent } from './components/staff-attendance/staff-attendance.component';
import { StaffPayrollComponent } from './components/staff-payroll/staff-payroll.component';
import { StaffOvertimeComponent } from './components/staff-overtime/staff-overtime.component';
import { StaffReportsComponent } from './components/staff-reports/staff-reports.component';
import { AddStaffComponent } from './components/add-staff/add-staff.component';
import { AddEntryComponent } from './components/add-entry/add-entry.component';
import { AssociateStaffComponent } from './components/staff-setup/associate-staff/associate-staff.component';
import { PersonalStaffComponent } from './components/staff-setup/personal-staff/personal-staff.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: StaffSetupComponent,
	canActivate: [AuthGuard],
	children : [
		{ path: '', redirectTo: 'association', pathMatch: 'full'},
		{ path: 'association', component: AssociateStaffComponent, canActivate: [AuthGuard] },
		{ path: 'personal', component: PersonalStaffComponent, canActivate: [AuthGuard] },
		{ path: '**', redirectTo: 'association', pathMatch: 'full'}
	]},
	{ path: 'maintain-staff', component: StaffMaintainStaffComponent, canActivate: [AuthGuard] },
	{ path: 'add-staff', component: AddStaffComponent, canActivate: [AuthGuard] },
	{ path: 'edit-staff/:id', component: AddStaffComponent, canActivate: [AuthGuard] },
	{ path: 'inactive-staff', component: StaffInactiveStaffComponent, canActivate: [AuthGuard] },
	{ path: 'overtime', component: StaffOvertimeComponent, canActivate: [AuthGuard] },
	{ path: 'attendance', component: StaffAttendanceComponent, canActivate: [AuthGuard] },
	{ path: 'add-entry', component: AddEntryComponent, canActivate: [AuthGuard] },
	{ path: 'edit-entry/:id', component: AddEntryComponent, canActivate: [AuthGuard] },
	{ path: 'payroll', component: StaffPayrollComponent, canActivate: [AuthGuard] },
	{ path: 'reports', component: StaffReportsComponent, canActivate: [AuthGuard] },
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class StaffManagerRoutingModule { }
