import { Component, OnInit, Injector, HostListener, ViewChild } from '@angular/core';
import { Router} from '@angular/router';

import { StaffService } from 'src/app/api/controllers/Staff';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { SessionService } from 'src/app/core/session/session.service';

import * as _ from 'underscore';

import * as moment from 'moment';
import _moment from "moment-timezone";

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { CommonConfirmModalComponent, ConfirmDialogModel } from 'src/app/shared/components/common-confirm-modal/common-confirm-modal.component';
import { MatDialog } from '@angular/material/dialog';

@Component({
  selector: 'app-staff-maintain-staff',
  templateUrl: './staff-maintain-staff.component.html',
  styleUrls: ['./staff-maintain-staff.component.scss']
})
export class StaffMaintainStaffComponent implements OnInit {

  staffListData:any;

  isStaffDataLoaded:boolean = false;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any = 0;
  itemLimit:any = 10;

  staff:any;
  staffSearch:string = "";

  modalService:any;

  totalFilterItems:any;
  visitTypeData:any;

  staffTypeData:any;
  staffOfficalCategoryData:any;
  staffPersonalCategoryData:any;
  staffTypeId:any;

  selectedInput:string = "";
  columnField: any = {}
  
  columnData:any
  gridSourceData:any

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  public params = {
		apartmentId: this.sessionService.apartmentId
	  }
  constructor(
    private injector: Injector,
    private staffService:StaffService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private sessionService: SessionService,
    private router:Router,
    public dialog: MatDialog,
  ) {
    this.modalService = this.injector.get(ModalService);
   }

  getPrintParams(event){
    this.datagrid.exportdata(event, 'Staff');
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  getStaffCategory(staff, id){
     
    var data = _.filter( this.staffTypeData, function( item ) {
      if(item.lookupValueId === id)
        return item;
    });

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.staffListData.map(obj=> {
        if(obj.staffId == staff.staffId){
          obj.staffCategory = data[0].lookupValueName;
        }
      });

      return data[0].lookupValueName.toLowerCase();
    }

  }

  getStaffSubCategory(staff, id) {
    if(this.staffTypeId == 115){

      var data = _.filter( this.staffOfficalCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
      } );
      if(data === undefined || data.length == 0){
        return '';
      }
      else {

        this.staffListData.map(obj=> {
          if(obj.staffId == staff.staffId){
            obj.staffSubCategory = data[0].lookupValueName;
          }
        });

        return data[0].lookupValueName;
      }

    }
    else {

      var data = _.filter( this.staffPersonalCategoryData, function( item ) {
      if(item.lookupValueId === id)
        return item;
      } );

      if(data === undefined || data.length == 0){
        return '';
      }
      else {

        this.staffListData.map(obj=> {
          if(obj.staffId == staff.staffId){
            obj.staffSubCategory = data[0].lookupValueName;
          }
        });

        return data[0].lookupValueName;
      }

    }
    

  }

  onGlSearchFilter(event) {

    if(event != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = event;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  
  @HostListener('window:onEditMainstaff', ['$event.detail'])
  onEditMainstaff(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let staffId = dataRecord.staffId
    this.router.navigateByUrl('/ams/staff/edit-staff/' + staffId);
  }
  
  @HostListener('window:onAllStaffDelete', ['$event.detail'])
  onAllStaffDelete(detail) {
    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let staffId = dataRecord.staffId

    const message = `Do you want to deactivate user?`;
    const dialogData = new ConfirmDialogModel("Confirm Action", message);
    const dialogRef = this.dialog.open(CommonConfirmModalComponent, {
      panelClass: 'material-dialog-medium',
      disableClose: true,
      data: dialogData
    });
    dialogRef.afterClosed().subscribe(dialogResult => {
      if (dialogResult) {
        let params = {
          staffId: staffId,
          deleteBy: this.sessionService.userId
        }
        this.staffService.deleteStaff(params).subscribe((res:any) => {
          this.sharedService.openSnackBar("Staff De-activated",'success');
          this.getAllStaff();
        }, error => {
          this.sharedService.openSnackBar('Server Error','error')
        });
      }
    });
  }
  ngOnInit() {

    
    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    

    this.columnData = [{
      text: 'StaffNo',
      datafield: 'serialNo',
      width: 150,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },{
      text: 'Staff Name',
      datafield: 'firstName',
      width: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    }, {
      text: 'Email Id',
      datafield: 'emailId',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'Job Title',
      datafield: 'staffCategory_Label',
      cellsrenderer: cellsrenderer,
      minwidth: 100,
      renderer: columnrenderer
    },{
      text: 'Phone Number',
      datafield: 'phoneNumber_Label',
      cellsrenderer: cellsrenderer,
      width: 150,
      renderer: columnrenderer
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"> <a href="javascript:void(0)" class="mr-2" onClick="editMainstaff('+ row +')"><i class="icon fa fa-pencil" aria-hidden="true"></i> </a> <a href="javascript:void(0)" onClick="onAllStaffDelete('+ row +')"><i class="fa fa-trash icon delete" aria-hidden="true"></i> </a></div>'
      },
      renderer: columnrenderer
    }];
    this.getAllStaff();
    let staffParams = {
      LookupTypeId: 25,
      ApartmentId: this.sessionService.apartmentId
    }

    //staff type
    this.lookupService.getLookupValueByLookupTypeId(staffParams).subscribe((res:any) => {
          
       this.staffTypeData = res.filter( item => {
          return item.isActive;
       });

    },
    error => {
      
    }
    );

    let officialParams = {
      LookupTypeId: 26,
      ApartmentId: this.sessionService.apartmentId
    }

    //offical category type
    this.lookupService.getLookupValueByLookupTypeId(officialParams).subscribe((res:any) => {
          
       this.staffOfficalCategoryData = res.filter( item => {
          return item.isActive;
       });

    },
    error => {
      
    }
    );

    let officialCategoryParams = {
      LookupTypeId: 27,
      ApartmentId: this.sessionService.apartmentId
    }

    //personal category type
    this.lookupService.getLookupValueByLookupTypeId(officialCategoryParams).subscribe((res:any) => {
          
       this.staffPersonalCategoryData = res.filter( item => {
          return item.isActive;
        });

    },
    error => {
      
    }
    );
  }

  getAllStaff(){
    this.staffService.getAllStaffsByApartmentId(this.params).subscribe((res:any) => {

      //filter active true items
      this.staffListData = res.filter(data => {
        return data.isActive
      });

      this.totalItems = this.staffListData.length;
  
      this.gridSourceData = {
        localdata: this.staffListData,
        datatype: "array"
      }
      this.staffListData = new jqx.dataAdapter(this.gridSourceData);
  
      this.isStaffDataLoaded = true;
  
    });
  }

}


function editMainstaff(row){
  var event = new CustomEvent('onEditMainstaff', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editMainstaff = editMainstaff;

function onAllStaffDelete(row){
  var event = new CustomEvent('onAllStaffDelete', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).onAllStaffDelete = onAllStaffDelete;